import React from 'react';
import { Datamap } from '../../engine/Datamap';
import Engine from '../../engine/Engine';
import { EVENT_RESULTS, Game_Event_Data as Game_Event_Info } from '../../engine/modules/GEvents';
import { flexRow, PrestigeUpgradeHave } from '../../CSSStyles';
import { canCheat } from '../../engine/externalfns/util';
import HumanPrefix from '../comps/HumanPrefix';
import PlanetDisplay from '../comps/PlanetDisplay';
import { unlockPrestigeUpgradeFormula } from '../../engine/modules/KnowledgeUpgrades';

interface CompProps {engine:Engine};
class PrestigeDiv extends React.Component<CompProps, {}> {

  constructor(props: CompProps) {
    super(props);
    
  }

  
  render() {
    const engine = this.props.engine
    const data = engine.datamap;
    let count = unlockPrestigeUpgradeFormula(data.events.knowledgePrestige.toNumber());

    return (
        <div className='NavColumn'> 
          <span>
          Available Points: {engine.knowledgeUpgrades.prestigePoints}
          </span>
          <span className='badButton'>
          <button>
            SEGMENT FAULT for no gain<br/>
            Reset prestige points
          </button>
          </span>
          <br/>
          {engine.knowledgeUpgrades.prestigeUpgrades.slice(0,count).map((ppu, index)=>{
            if (ppu.have()) {
              return <button disabled key={`prestigeupgrade`+index} style={PrestigeUpgradeHave}>{ppu.name}</button>
            }
            const able = engine.knowledgeUpgrades.prestigePoints >=  ppu.cost
            return (<button key={`prestigeupgrade`+index} disabled={!able} onClick={ppu.action}>
              {ppu.name}<br/>
              Cost: {ppu.cost}
            </button>)
          })}
          <br/><br/>
        </div>
    );
  }
}

export default PrestigeDiv;