import _ from "lodash";
import CoreEngine from "./CoreEngine";
import { newDefaultMap } from "./Datamap";
import EnergyModule from "./modules/EnergyModule";
import FirstCommands from "./modules/FirstCommands";
import Spells from "./modules/Spells";
import ActiveMemory from "./modules/ActiveMemory";
import GEvents from "./modules/GEvents";
import KnowledgeUpgrades from "./modules/KnowledgeUpgrades";


export const AUTOSAVE_INTERVAL = 1000 * 60;

export default class Engine extends CoreEngine {

    firstCommands: FirstCommands = new FirstCommands(this);
    energyModule: EnergyModule = new EnergyModule(this);
    spells: Spells = new Spells(this);
    memory: ActiveMemory = new ActiveMemory(this);
    events: GEvents = new GEvents(this);
    knowledgeUpgrades: KnowledgeUpgrades = new KnowledgeUpgrades(this);

    processDelta = (delta: number) => {

        this.memory.mainDelta(delta)
        this.events.delta(delta);

        if (((this.datamap.autosaveCounter += delta) > AUTOSAVE_INTERVAL) && this.datamap.autosave === true) {

            this.save();
            this.datamap.autosaveCounter = 0;
        }
    }

    import = (basedstr: string) => {
        this.datamap.autosave = false;

        //var LZUTF8 = require('lzutf8');

        //console.log(lzutf8str);

        //let arrayStr = lzutf8str.split(',')
        //let arrayInt = arrayStr.map(value => parseInt(value))
        //let magicArray = Uint8Array.of(...arrayInt);


        //var string = new TextDecoder("utf-8").decode(lzutf8str);
        //let test = new Uint8Array(lzutf8str)
        //let uint8array: Uint8Array = new Uint8Array(lzutf8str)
        //let uint8array = new Uint8Array()
        //var uint8array = new TextEncoder().encode(lzutf8str);
        //console.log(uint8array);

        //var output = LZUTF8.encodeUTF8(lzutf8str);
        //console.log(output, arrayStr, magicArray);

        //        let check = LZUTF8.decompress(magicArray);
        //console.log(check);

        /**
         * 
      */
        let plain = window.atob(basedstr);


        let combstr = plain;
        if (combstr != null) {
            let oldobj = JSON.parse(combstr);
            this.datamap = newDefaultMap();
            _.merge(this.datamap, oldobj)
        }

        this.datamap.autosave = false;
        this.checkSet();
        this.extraLoad();

    }

    export = () => {
        let dataString = JSON.stringify(this.datamap);
        let based = window.btoa(dataString);
        //var LZUTF8 = require('lzutf8');
        //let output = LZUTF8.compress(dataString);
        //  console.log(output)
        //console.log(dataString.length)

        //        download(`prophet_export_${Date.now()}.txt`,dataString)
        download(`stca_export_${Date.now()}.txt`, based)

        function download(filename: string, text: string) {
            var element = document.createElement('a');
            element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(text));
            element.setAttribute('download', filename);

            element.style.display = 'none';
            document.body.appendChild(element);

            element.click();

            document.body.removeChild(element);
        }

    }

    autosaveToggle = () => {
        this.datamap.autosaveCounter = 0;
        this.datamap.autosave = !this.datamap.autosave;
        this.notify();
    }

    extraLoad = () => {
        console.log('EXTRALOAD');

    }

}

