import Decimal from "break_infinity.js";
import CURRENCY_FUNCTIONS from "../decimalInterfaces/currency_functionts";
import Decimal_ExpoCommand from "../decimalInterfaces/Decimal_ExpoCommand";
import Engine from "../Engine";

export default class FirstCommands {
    constructor(public engine: Engine) {

    }

    create_energy_DEC: Decimal_ExpoCommand = {
        expo: {
            coefficient: 1.5,
            initial: 1,
        },
        countFns: {
            gain: (data, amount) => { 
                data.decimals.energy = data.decimals.energy.add(amount)
            },
            get: (data) => data.decimals.energy
        },
        currencyFns: CURRENCY_FUNCTIONS.effortCurrencyFN,
        hidden: () => false,
        name: "+1 X",
    }

    create_dx_fromY: Decimal_ExpoCommand = {
        expo: {
            coefficient: 1.5,
            initial: 1,
        },
        countFns: {
            gain: (data, amount) => { 
                data.decimals.dxfromy = data.decimals.dxfromy.add(amount)
            },
            get: (data) => data.decimals.dxfromy
        },
        currencyFns: CURRENCY_FUNCTIONS.yCurrencyFN,
        hidden: () => this.engine.datamap.events.knowledgePrestige.lessThan(2),
        name: "X Power",
    }

    create_dx_fromZ: Decimal_ExpoCommand = {
        expo: {
            coefficient: 1.5,
            initial: 1,
        },
        countFns: {
            gain: (data, amount) => { 
                data.decimals.dxfromz = data.decimals.dxfromz.add(amount)
            },
            get: (data) => data.decimals.dxfromz
        },
        currencyFns: CURRENCY_FUNCTIONS.ZCurrencyFN,
        hidden: () => this.engine.datamap.events.knowledgePrestige.lessThan(4),
        name: "X Power",
    }

    create_momentum_DEC: Decimal_ExpoCommand = {
        expo: {
            coefficient: 1.3,
            initial: 16,
        },
        countFns: {
            gain: (data, amount) => { 
                this.t2Prestige();
                data.decimals.momentum = data.decimals.momentum.add(amount)
            },
            get: (data) => data.decimals.momentum
        },
        currencyFns: CURRENCY_FUNCTIONS.energyCurrencyFN,
        hidden: () => false,
        name: "Prestige for X Power",
    }

    toggleCheating = () => {
        this.engine.memory.cheating = !this.engine.memory.cheating;
        this.engine.notify();
    }

    create_charger_DEC: Decimal_ExpoCommand = {
        expo: {
            coefficient: 1.4,
            initial: 15,
        },
        countFns: {
            gain: (data, amount) => { 
                this.t1Prestige();
                data.decimals.chargers = data.decimals.chargers.add(amount)
            },
            get: (data) => data.decimals.chargers
        },
        currencyFns: CURRENCY_FUNCTIONS.energyCurrencyFN,
        hidden: (data) => data.decimals.momentum.lessThan(3),
        name: "Prestige for Y",
    }

    create_energizer_DEC: Decimal_ExpoCommand = {
        expo: {
            coefficient: 1.2,
            initial: 1000,
        },
        countFns: {
            gain: (data, amount) => { 
                data.decimals.energizers = data.decimals.energizers.add(amount)
            },
            get: (data) => data.decimals.energizers
        },
        currencyFns: CURRENCY_FUNCTIONS.effortCurrencyFN,
        hidden: (data) => data.decimals.momentum.lessThan(6),
        name: "Create Charge Booster",
    }

    clearCharge = () => {
        const data = this.engine.datamap;
        data.decimals.charge = new Decimal(0);
    }

    t1Prestige = () => {
        const data = this.engine.datamap;

        data.decimals.effort = new Decimal(1);
        data.decimals.energy = new Decimal(0);
        data.decimals.mana = new Decimal(0);
        data.decimals.energizers = new Decimal(0);
        data.decimals.chargers = new Decimal(0);

        data.decimals.dxfromy = new Decimal(0);

        this.engine.spells.clearTimers();
        this.clearCharge();
    }

    t2Prestige = () => {
        const data = this.engine.datamap;
        data.decimals.chargers = new Decimal(0);

        data.decimals.dxfromz = new Decimal(0);

        this.t1Prestige();
    }

}