import Engine from "../Engine";

export default class EnergyModule {
    constructor(public engine: Engine) {

    }

    discharge = () => {
        let gain = this.dischargeValue();
        if (this.engine.datamap.spellData.manaUnlocked) {
            let newZ = this.engine.datamap.decimals.charge;
            if (this.engine.datamap.events.prestigeUpgrades.doubleZgain) newZ = newZ.times(2);
            if (this.engine.datamap.events.prestigeUpgrades.tripleZgain) newZ = newZ.times(3);
            if (this.engine.datamap.events.prestigeUpgrades.quadrupleZGain) newZ = newZ.times(4);
            if (this.engine.datamap.events.prestigeUpgrades.squareZgain) newZ = newZ.times(newZ);
           this.engine.datamap.decimals.mana = newZ;
        }
        this.engine.firstCommands.clearCharge();
        this.engine.datamap.decimals.effort = this.engine.datamap.decimals.effort.add(gain);
        this.engine.notify();
    }

    dischargeValue = () => {
        let decimals = this.engine.datamap.decimals;
        let energy_base = decimals.energy;
        let charge_mult = decimals.charge;
        let momentum_mult = decimals.momentum;

        
        let gain = energy_base.times(charge_mult).times(momentum_mult);
        
        if (this.engine.datamap.spellData.dischargeUpgrades.greaterThan(0)) gain = gain.times(this.engine.datamap.spellData.dischargeUpgrades.add(1))
        if (this.engine.datamap.spellData.dischargeUpgrade1) gain = gain.times(10)
        if (this.engine.datamap.events.prestigeUpgrades.dischargeUpgrade) gain = gain.times(10)
        //pplet gain = this.engine.datamap.decimals.charge.times(this.engine.firstCommands.gainPerSecond).times(10);
        /*if (this.engine.datamap.decimals.mana.greaterThan(0)) {
            gain = gain.times(this.engine.datamap.decimals.mana.add(1))
        }*/
        return gain
    }
}