import React from 'react';
import { Datamap } from '../../engine/Datamap';
import Engine from '../../engine/Engine';
import { EVENT_RESULTS, Game_Event_Data as Game_Event_Info } from '../../engine/modules/GEvents';
import { flexRow } from '../../CSSStyles';
import { canCheat } from '../../engine/externalfns/util';
import HumanPrefix from '../comps/HumanPrefix';
import PlanetDisplay from '../comps/PlanetDisplay';

interface CompProps {engine:Engine, data:Datamap};
class EventRow extends React.Component<CompProps, {tools: boolean}> {

  constructor(props: CompProps) {
    super(props);
    this.state = {
      tools: false,
    }
  }

  clearEvent = () => {
    this.props.engine.events.cleanupEvent();
    this.props.engine.notify();
  } 

  toggleTools = () => {
    this.setState({tools:!this.state.tools})
  }

  forceEvent = () => {
    this.props.engine.events.triggerEventChance();
    this.props.engine.notify();
  }
  
  render() {
    const engine = this.props.engine;
    const data = this.props.data;

    const eventData = data.events
    const ID = eventData.eventID;
    const chosenEvent = engine.events.eventList[ID];

    

    return (
      <div style={{
        backgroundColor:"black",
        border: '2px solid #87d441',
        padding: '2px'
      }}>
    {(ID === 0)&&<div><HumanPrefix/> {EVENT_RESULTS[eventData.lastEventResult]}</div>}
        {(ID != 0)&&<EventDiv ed={chosenEvent} engine={engine}/>}
      
        {(canCheat())&&<div>

        {(!this.state.tools)&&<button onClick={this.toggleTools}>Show Tools</button>}
        {(this.state.tools)&&
        <div>
        <div style={flexRow}>
          <button onClick={this.toggleTools}>Hide Tools</button>
          <button children={'Clear Event'} onClick={this.clearEvent} />          
          <button children={'Force Event'} onClick={this.forceEvent} />          
        </div>
        {JSON.stringify(eventData)}
        </div>
        }
        </div>}
      
      </div>

        
    );
  }
}

export default EventRow;

class EventDiv extends React.Component<{ed:Game_Event_Info, engine: Engine},{}> {
  render () {

    let extra = extraEventText(this.props.engine.datamap)

    return (
      <div>
        <span className='Tuser' style={{fontWeight:'bold'}}>
          {this.props.ed.name}
        </span><br/>
        <span>
          <HumanPrefix/> {this.props.ed.description}
        </span><br/>
        {extra&& <span className='Tlocation'>{extra}</span>}
        <div style={{
          display: "flex",
          flexDirection: "row",
          gap: '5px',
        }}>
          {this.props.ed.choices.map((ec,index)=>{
            return (
              <button onClick={ec.action} key={`eventchoice${index}`}>
                {ec.label}
              </button>
            )
          })}
          {specialButton(this.props.engine)}
        </div>
      </div>
    )
  }
}

function specialButton (engine: Engine) {
  const data = engine.datamap;
  
  if (engine.events.exam1condition()) {
    return <button onClick={engine.events.passExam1}>
      None
    </button>
  }

  return null;
}


export function extraEventText (data: Datamap ) {
  const eventData = data.events;
  const id = eventData.eventID;
  
  if (id === 1) {
    return `> Basket A has ${eventData.eventData[0]} apples. Basket B has ${eventData.eventData[1]} apples. Basket C has ${eventData.eventData[2]} apples.`
  } else if (id === 2) {
    return `> Picture A is a ${eventData.eventData[0]}% match. Picture B is a ${eventData.eventData[1]}% match. Picture C is a ${eventData.eventData[2]}% match.`
  } else if (id === 3) {
    if (eventData.examProgress === 0 )return `> Total Failures: ${data.events.examFails}`
    else return "> Exam already passed."
  } else if (id === 4 || id === 5 || id === 6 || id === 7 || id === 8) {
    return <div>
            <span>{'> Probe Senses'}</span>
      <div className='PlanetRow'>
      <PlanetDisplay data={data} planet={eventData.eventData[0]}/>
      <PlanetDisplay data={data} planet={eventData.eventData[1]}/>
      <PlanetDisplay data={data} planet={eventData.eventData[2]}/>
      <PlanetDisplay data={data} planet={eventData.eventData[3]}/>
    </div>
      </div>
  } else if (id === 9) {
    return `> Tank A has ${eventData.eventData[0]/7}*7% oxygen. Tank B has ${eventData.eventData[1]}% oxygen. Tank C has ${eventData.eventData[2]/5}*5% oxygen.`
  } else return 'ERROR'
}