import Decimal from "break_infinity.js";
import Engine from "../Engine";

export default class ActiveMemory {
    constructor (public engine: Engine) {
        
    }

    deltaX: Decimal = new Decimal(0);
    deltaY: Decimal = new Decimal(0);
    cheating: boolean = false;

    mainDelta = (delta:number) => {
        //OPTIMIZE ME!!!
        const data = this.engine.datamap;

        
        const effort = data.decimals.effort;
        const energy = data.decimals.energy;
        const momentum = data.decimals.momentum;
        const chargers = data.decimals.chargers;
        const energizers = data.decimals.energizers;
        const prestigeUpgrades = data.events.prestigeUpgrades;
        
        //all O(1) ya?
        
        let delta_in_seconds = delta / 1000;
        if (this.cheating) {
            delta_in_seconds = delta_in_seconds * 60;
        }
        

        let gain = energy.times(delta_in_seconds);

        if (chargers.greaterThan(0)) {
            let deltaY = chargers.times(delta_in_seconds).div(60);
            if (energizers.greaterThan(0)) deltaY = deltaY.times(energizers)

            if (prestigeUpgrades.doubleYgain) deltaY = deltaY.times(2);
            if (prestigeUpgrades.tripleYgain) deltaY = deltaY.times(3);
            if (prestigeUpgrades.quadrupleYGain) deltaY = deltaY.times(4);
            if (prestigeUpgrades.squareXgain) deltaY = deltaY.times(deltaY);
            
            this.deltaY = deltaY.times(1000).div(delta);
            const newY = data.decimals.charge.add(deltaY);
            data.decimals.charge = newY;

            //gain = gain.times(data.decimals.charge.add(1))
        }
        
        if (momentum.greaterThan(0)) {
            gain = gain.times(momentum.add(1))
        }

        if (data.events.upgradeDX.greaterThan(0)) {
            gain = gain.times(data.events.upgradeDX.add(1))
        }

        if (data.spellTimers.spell1 > 0) {
            let maxDelta = Math.min(data.spellTimers.spell1, delta);
            data.spellTimers.spell1 = Math.max(0, data.spellTimers.spell1 - delta);
            let pow = 10;
            if (this.engine.datamap.events.prestigeUpgrades.plusDeltaXSpellPowerUpgrade) pow = pow * 2;
            gain = gain.times(pow).times(maxDelta).div(delta);
        }

        if (prestigeUpgrades.doubleXgain) gain = gain.times(2);
        if (prestigeUpgrades.tripleXgain) gain = gain.times(3);
        if (prestigeUpgrades.quadrupleXGain) gain = gain.times(4);
        if (prestigeUpgrades.squareXgain) gain = gain.times(gain);

        if (data.decimals.dxfromy.greaterThan(0)) gain = gain.times(data.decimals.dxfromy.add(1))
        if (data.decimals.dxfromz.greaterThan(0)) gain = gain.times(data.decimals.dxfromz.add(1))


        this.deltaX = gain.times(1000).div(delta);
        data.decimals.effort = effort.add(gain);
    }

}