import Decimal from 'break_infinity.js';
import React from 'react';
import { Datamap } from '../../engine/Datamap';
import Engine from '../../engine/Engine';
import DisplayDecimal from '../comps/DisplayDecimal';
import ListedResource, { ListedResourceNumber } from '../comps/ListedResource';


interface CompProps {engine:Engine, data:Datamap};
class ResourceColumn extends React.Component<CompProps, {}> {

  render() {
    let engine = this.props.engine;
    let data = this.props.data;
    return (
      <div className='ResourceColumn'>
        <ListedResource decimal={data.decimals.effort} name={'X'} />
        <ListedResource decimal={data.decimals.charge} name={'Y'} hide={0} />
        <ListedResource decimal={data.decimals.mana} name={'Z'} hide={0} />
        <hr/>
        {/*<ListedResource decimal={data.decimals.energy} name={'X'} hide={0} />
        <ListedResource decimal={data.decimals.chargers} name={'Y'} hide={0} />
        <ListedResource decimal={data.decimals.momentum} name={'XP'} hide={0} />
        <ListedResource decimal={data.decimals.energizers} name={'Boosters'} hide={0} />
        <hr/>*/}
        <ListedResource decimal={engine.memory.deltaX} name={'X /s'} />
        <ListedResource decimal={engine.memory.deltaY} name={'Y /s'} />
        <hr/>
        <ListedResourceNumber num={data.events.eventVictories} name={''} hide={0} />  
        <ListedResourceNumber num={data.events.examProgress} name={''} hide={0} />  
        <ListedResource decimal={data.events.knowledge} name={''} hide={0} />   
        <hr/>
        <ListedResourceNumber num={data.spellTimers.spell1/1000} name='+Xs' hide={0} />
      </div>
        
    );
  }
}

export default ResourceColumn;