import React from 'react';

import Engine from './engine/Engine';
import { Datamap } from './engine/Datamap';
import { flexRow, spaceBetween } from './CSSStyles';
import './App.css';
import HeaderRow from './reactui/layout/HeaderRow';
import DisplayDecimal from './reactui/comps/DisplayDecimal';
import ExpoButton from './reactui/comps/GoodExpoButton';
import MainContainer from './reactui/layout/MainContainer';


interface AppProps { engine: Engine };
export interface AppState {
  data: Datamap
};
class App extends React.Component<AppProps, AppState> {

  constructor(props: AppProps) {
    super(props);

    this.state = {
      data: this.props.engine.datamap,
    }
  }


  notify = () => {
    this.setState({
      data: this.props.engine.datamap,
    })
  }



  componentDidMount() {
    const engine = this.props.engine;
    
    engine.setNotify(this.notify)

    engine.extraLoad();
    engine.notify();

    engine.start();
  }



  render() {
    let state = this.state;
    let engine = this.props.engine;
    let data = state.data;
    return (<div className='AppThree'>
      <HeaderRow engine={engine} data={data} />
      <MainContainer engine={engine} data={data} />      
    </div>
    );
  }
}

export default App;