import Decimal, { DecimalSource } from "break_infinity.js";
import React from "react";
import DisplayDecimal from "./DisplayDecimal";
import SCIENNOT from "./Notation";

const ListedResource: React.FC<{ name: string, decimal: Decimal, hide?: number }> = (props) => {

    const dec = props.decimal;
    if (props.hide != undefined) if (Decimal.eq(dec, props.hide)) return null;
    return (
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
            <span style={{ marginRight: '5px' }} className='resourceColor'>
                {props.name}
            </span>
            <span>
                <DisplayDecimal decimal={dec} />
            </span>
        </div>
    )
}

export default ListedResource;

export const ListedResourceNumber: React.FC<{ name: string, num: number, hide?: number, percent?: boolean }> = (props) => {

    const dec = props.num;
    if (props.hide != undefined) if (Decimal.eq(dec, props.hide)) return null;
    return (
        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
            <span style={{ marginRight: '5px' }} className='resourceColor'>
                {props.name}
            </span>
            <span>
                {dec}{props.percent?'%':''}
            </span>
        </div>
    )
}