import Decimal from "break_infinity.js";

export interface Datamap {
    autosave: boolean;
    autosaveCounter: number;
    last: number;
    decimals: {
        effort: Decimal;
        energy: Decimal;
        dxfromy: Decimal;
        dxfromz: Decimal;
        momentum: Decimal;
        chargers: Decimal;
        charge: Decimal;
        energizers: Decimal;
        mana: Decimal;
    }
    spellData: {
        manaUnlocked: boolean,
        spell1Unlocked: boolean,
        dischargeUpgrade1: boolean,
        dischargeUpgrades: Decimal,
    }
    spellTimers: {
        spell1: number;
        spell2: number;
        spell3: number;
        spell4: number;
        spell5: number;
    };
    events: {
        eventTimeCounter: number;

        lastEventResult: number;
        
        eventID: number;
        eventRNG: number;
        eventData: any[];
        
        eventVictories:number;
        examFails:number;
        examProgress: number;

        knowledge: Decimal;
        upgradeDX: Decimal;

        knowledgePrestige: Decimal;
        prestigeUpgrades: {
            doubleLearningProgress: boolean,
            doubleKnowledgeGain: boolean,
            skipExam1: boolean,
            dischargeUpgrade: boolean,
            plusDeltaXSpellPowerUpgrade: boolean,
            plusDeltaXSpellDurationUpgrade: boolean,
            unlockEventSpell: boolean,
            doubleEventCooldownRecovery: boolean,
            
            quadrupleXGain: boolean,
            doubleXgain: boolean,
            tripleXgain: boolean,
            squareXgain: boolean,

            quadrupleYGain: boolean,
            doubleYgain: boolean,
            tripleYgain: boolean,
            squareYgain: boolean,
            
            quadrupleZGain: boolean,
            doubleZgain: boolean,
            tripleZgain: boolean,
            squareZgain: boolean,
        }
    }
}

export function newDefaultMap () {
    let map: Datamap = {
        autosave: false,
        autosaveCounter: 0,
        last: Date.now(),
        decimals: {
            effort: new Decimal(1),
            energy: new Decimal(0),
            dxfromy: new Decimal(0),
            dxfromz: new Decimal(0),
            momentum: new Decimal(0),
            chargers: new Decimal(0),
            charge: new Decimal(0),
            energizers: new Decimal(0),
            mana: new Decimal(0),
        },
        spellData: {
            manaUnlocked: false,
            spell1Unlocked: false,
            dischargeUpgrade1: false,
            dischargeUpgrades: new Decimal(0)
        },
        spellTimers: {
            spell1: 0,
            spell2: 0,
            spell3: 0,
            spell4: 0,
            spell5: 0,
        },
        events: {
            eventData: [],
            eventID: 0,
            eventRNG: 0,
            eventTimeCounter: 0,
            lastEventResult: 0,
            eventVictories: 0,
            examFails: 0,
            examProgress: 0,
            knowledge: new Decimal(0),
            upgradeDX: new Decimal(0),
            knowledgePrestige: new Decimal(0),
            prestigeUpgrades: prestigeUpgrades_Default(),

        }
    }
    return map;
}

export function setDecimals(data:Datamap) {
    data.decimals.effort = new Decimal(data.decimals.effort);
    data.decimals.energy = new Decimal(data.decimals.energy);
    data.decimals.dxfromz = new Decimal(data.decimals.dxfromz);
    data.decimals.dxfromy = new Decimal(data.decimals.dxfromy);
    data.decimals.momentum = new Decimal(data.decimals.momentum);
    data.decimals.charge = new Decimal(data.decimals.charge);
    data.decimals.chargers = new Decimal(data.decimals.chargers);
    data.decimals.energizers = new Decimal(data.decimals.energizers);
    data.decimals.mana = new Decimal(data.decimals.mana);
    data.events.knowledge = new Decimal(data.events.knowledge);
    data.events.upgradeDX = new Decimal(data.events.upgradeDX);
    data.events.knowledgePrestige = new Decimal(data.events.knowledgePrestige);
    data.spellData.dischargeUpgrades = new Decimal(data.spellData.dischargeUpgrades);
}

