export interface PrestigePointUpgrade {
    name: string;
    action: VoidFunction;
    cost: number;
    have: ()=>boolean;
}

export function unlockPrestigeUpgradeFormula (prestiges: number):number {
    let base = prestiges - 2;
    let each = base * 2
    return Math.max(0,each);
}