import { getRandomInt } from "../externalfns/util";

export interface Planet {
    oxygenEarthPercentage: number;
    gravityEarthPercentage: number;
    extremeHighTemp: number;
    waterSurfacePercentage: number;
    extremeLowTemp: number;
    toxicityLevel: number;
    surfaceArea: number;
}

export function getRandomPlanet (): Planet {
    return {
        extremeHighTemp: getRandomInt(100,200),
        extremeLowTemp: getRandomInt(-100,0),
        gravityEarthPercentage: getRandomInt(1,10000),
        oxygenEarthPercentage: getRandomInt(0,500),
        surfaceArea: getRandomInt(1000000,10000000000),
        toxicityLevel: getRandomInt(0, 100),
        waterSurfacePercentage: getRandomInt(0, 100),
    }
}

export function oxygenOptimal (planet: Planet) {
    return Math.abs(planet.oxygenEarthPercentage - 100);
}
