import React from "react";
import { Datamap } from "../../engine/Datamap";
import Decimal_ExpoCommand, { expoCommand_Able, expoCommand_Cost, expoCommand_maxAmmount, expoCommand_buy, expoCommand_buyMax } from "../../engine/decimalInterfaces/Decimal_ExpoCommand";
import { BasicCommand, CommandGodDamn } from "../../engine/types/BasicCommand";
import DisplayDecimal from "./DisplayDecimal";


interface ExpoButtonProps {
  command: CommandGodDamn,
}

const CommandGDButton: React.FC<ExpoButtonProps> = (props) => {

  const command = props.command
  if (command.hidden()) return null;

  return (
    <div>
        <button disabled={!command.able()} onClick={command.command} >
          {command.label} <br/>
          {command.description()}
        </button>
    </div >
  )
}
export default CommandGDButton;
