import React, { ReactNode } from 'react';
import './CommandButtonFC.css'
import { CommandGodDamn } from '../../engine/types/BasicCommand';
import CommandGDButton from './CommandGDButton';
import { flexRow, spaceBetween } from '../../CSSStyles';

export interface CCBProps {
  div: ReactNode;
  buttonText: string;
}

export default class PopUpDiv extends React.Component<CCBProps, {open:boolean}>{ 

  constructor(props:CCBProps) {
    super(props)

    this.state = {
      open: false
    }

  }

  fakeBuy = (ev: React.MouseEvent) => {
    this.setState({open:!this.state.open})
  }

  render () {
    const props = this.props;

    return (<span>
        <span className='FakeButton'>
        <button className='DCommandFC' onClick={this.fakeBuy}>
          {props.buttonText}
        </button>
        </span>
        <div className='WarningBox' hidden={!this.state.open}>
            <div className='WarningOverlay' onClick={this.fakeBuy}>
              <div className='WarningPopup'>
                {this.props.div}
                <div style={flexRow}>
                <button className='PlainD' onClick={this.fakeBuy}>
                  Close
                </button>
                </div>
              </div>
            </div>
        </div>
    </span>)
  }
}


