import Decimal from "break_infinity.js";
import React from "react";
import Engine from "../../engine/Engine";
import { ableToCastSpell, Spell } from "../../engine/modules/Spells";
import DisplayDecimal from "./DisplayDecimal";

const SpellDisplay: React.FC<{ spell:Spell, engine:Engine }> = (props) => {
    const spell = props.spell;
    const engine = props.engine;
    const data = engine.datamap;
    const able = ableToCastSpell(spell, data.decimals.mana)
    if (spell.hidden(data)) return null;
    return (
        <div>
            <button disabled={!able} onClick={()=>{engine.spells.castSpell(spell)}}>
                {spell.name}<br/>
                {spell.manaCost} Z
            </button>
        </div>
    )
}

export default SpellDisplay;