import React, { ReactNode } from 'react';
import './CommandButtonFC.css'
import { CommandGodDamn } from '../../engine/types/BasicCommand';
import CommandGDButton from './CommandGDButton';
import { flexRow, spaceBetween } from '../../CSSStyles';

export interface CCBProps {
  command: CommandGodDamn;
  warning: string
}

export default class ConfirmCommandButton extends React.Component<CCBProps, {open:boolean}>{ 

  constructor(props:CCBProps) {
    super(props)

    this.state = {
      open: false
    }

  }

  fakeBuy = (ev: React.MouseEvent) => {
    if (ev.shiftKey) {
      this.context.submitCommand(this.props.command.command)
    } else this.setState({open:!this.state.open})
    
  }

  render () {
    const props = this.props;
    const command = props.command
    if (command.hidden()) return null;

    return (<span>
        <span className='FakeButton'>
        <button className='DCommandFC' disabled={!command.able()} onClick={this.fakeBuy}>
          {command.label} <br/>
          {command.description()}
        </button>
        </span>
        <div className='WarningBox' hidden={!this.state.open}>
            <div className='WarningOverlay' onClick={this.fakeBuy}>
              <div className='WarningPopup'>
                {this.props.warning}
                <div style={flexRow}>
                <CommandGDButton command={command}/>
                <button className='PlainD' onClick={this.fakeBuy}>
                  Cancel
                </button>
                </div>
              </div>
            </div>
        </div>
    </span>)
  }
}


