import Decimal from "break_infinity.js";
import { timeStamp } from "console";
import { Datamap } from "../Datamap";
import CURRENCY_FUNCTIONS from "../decimalInterfaces/currency_functionts";
import Decimal_ExpoCommand from "../decimalInterfaces/Decimal_ExpoCommand";
import Engine from "../Engine";
import { BasicCommand, CommandGodDamn } from "../types/BasicCommand";


export interface Spell {
    name: string
    manaCost: number
    action: VoidFunction
    hidden: (data:Datamap)=>boolean
}

export function ableToCastSpell (spell: Spell, resource: Decimal) {
    return resource.greaterThanOrEqualTo(spell.manaCost)
}

export default class Spells {
    constructor(public engine: Engine) {

    }

    get charge () {
        return this.engine.datamap.decimals.charge;
    }

    set charge (newCharge: Decimal) {
        this.engine.datamap.decimals.charge = newCharge;
    }

    get mana () {
        return this.engine.datamap.decimals.mana
    }

    set mana (newValue: Decimal) {
        this.engine.datamap.decimals.mana = newValue
    }

    castSpell = (spell:Spell) => {
        if (ableToCastSpell(spell, this.mana)) {
            this.mana = this.mana.minus(spell.manaCost)
            spell.action();
            this.engine.notify();
        }
    }

    cast_increaseEffort = () => {
        let gain = 60 * 1000;
        if (this.engine.datamap.events.prestigeUpgrades.plusDeltaXSpellDurationUpgrade) gain = gain * 2
        this.engine.datamap.spellTimers.spell1 += gain

    }

    cast_forceEvent =() => {

    }

    increaseEffort : Spell = {
        name: "Cast +X",
        action: this.cast_increaseEffort,
        manaCost: 10,
        hidden: (data)=>!data.spellData.spell1Unlocked,
    }

    spell_stca : Spell = {
        name: "STCA",
        action: this.cast_forceEvent,
        manaCost: 100,
        hidden: (data)=>!data.events.prestigeUpgrades.unlockEventSpell,
    }
    
    unlock_mana: CommandGodDamn = {
        command: ()=>{
            this.engine.datamap.decimals.charge = this.engine.datamap.decimals.charge.minus(100);
            this.engine.datamap.spellData.manaUnlocked = true;
            this.engine.notify();
        },
        label: 'Unlock Z',
        description: ()=>'100 Y',
        able: ()=>this.engine.datamap.decimals.charge.greaterThanOrEqualTo(100),
        hidden: ()=>this.engine.datamap.decimals.chargers.lessThan(3) || this.engine.datamap.spellData.manaUnlocked
    }

    unlock_spell1: CommandGodDamn = {
        command: ()=>{
            this.engine.datamap.decimals.mana = this.engine.datamap.decimals.mana.minus(100);
            this.engine.datamap.spellData.spell1Unlocked = true;
            this.engine.notify();
        },
        label: 'Unlock Z Spell [+X]',
        description: ()=>'100 Z',
        able: ()=>this.engine.datamap.decimals.mana.greaterThanOrEqualTo(100),
        hidden: ()=>(this.engine.datamap.spellData.manaUnlocked === false) || this.engine.datamap.spellData.spell1Unlocked,
    }

    upgrade_discharge_1: CommandGodDamn = {
        command: ()=>{
            this.engine.datamap.decimals.mana = this.engine.datamap.decimals.mana.minus(100);
            this.engine.datamap.spellData.dischargeUpgrade1 = true;
            this.engine.notify();
        },
        label: 'Upgrade Dicharge',
        description: ()=>'100 Z',
        able: ()=>this.engine.datamap.decimals.mana.greaterThanOrEqualTo(100),
        hidden: ()=>(this.engine.datamap.decimals.momentum.lessThan(4)) || this.engine.datamap.spellData.dischargeUpgrade1,
    }

    clearTimers = () => {
        const spellTimers = this.engine.datamap.spellTimers;
        spellTimers.spell1 = 0;
        spellTimers.spell2 = 0;
        spellTimers.spell3 = 0;
        spellTimers.spell4 = 0;
        spellTimers.spell5 = 0;
    }

    upgradeDischarge: Decimal_ExpoCommand = {
        expo: {
            coefficient: 1.3,
            initial: 10,
        },
        countFns: {
            gain: (data, amount) => { 
                data.spellData.dischargeUpgrades = data.spellData.dischargeUpgrades.add(amount)
            },
            get: (data) => data.spellData.dischargeUpgrades
        },
        currencyFns: CURRENCY_FUNCTIONS.ZCurrencyFN,
        hidden: (data) => data.events.examProgress === 0 && data.spellData.dischargeUpgrades.eq(0),
        name: "Upgrade Discharge",
    }

}