import React from "react";
import { Datamap } from "../../engine/Datamap";
import Decimal_ExpoCommand, { expoCommand_Able, expoCommand_Cost, expoCommand_maxAmmount, expoCommand_buy, expoCommand_buyMax, expoCommand_maxCost } from "../../engine/decimalInterfaces/Decimal_ExpoCommand";
import { expoI_maxBuyCost } from "../../engine/decimalInterfaces/ExpoCurve";
import Engine from "../../engine/Engine";
import DisplayDecimal from "./DisplayDecimal";


interface ExpoButtonProps {
  DEC: Decimal_ExpoCommand,
  engine: Engine,
  max?: boolean,
}

const ExpoButton: React.FC<ExpoButtonProps> = (props) => {

  const data = props.engine.datamap;
  const notify = props.engine.notify;
  const dec = props.DEC;
  let name = dec.name;
  let level = dec.countFns.get(data);

  let change = dec.change ? dec.change : undefined;

  let able = expoCommand_Able(data, dec)
  let cost = expoCommand_Cost(data, dec)
  let max = expoCommand_maxAmmount(data, dec)
  let max_cost = expoCommand_maxCost(data, dec)

  return (
    <div hidden={dec.hidden ? dec.hidden(data) : false}>
        <button disabled={!able} onClick={() => {expoCommand_buy(data, dec);notify()}} >
          {name} (<DisplayDecimal decimal={level} />) <br/>
          Cost: <DisplayDecimal decimal={cost}/> {dec.currencyFns.name}
        </button>
        {props.max && <button  disabled={!able} onClick={() => { expoCommand_buyMax(data, dec); notify() }}>
          Max (+<DisplayDecimal decimal={max} />)<br/>
          Cost: <DisplayDecimal decimal={max_cost} />
        </button>}
    </div >
  )
}
export default ExpoButton;
