import { Datamap } from "../Datamap";

export interface BasicCommand {
    label: string;
    command: ()=>void;
    description?: string;
    hidden?: boolean;
}

export interface CommandGodDamn {
    label: string;
    description: ()=>string;
    command: ()=>void;
    able: ()=>boolean;
    hidden: ()=>boolean;
}

export function makeBasicCommand (label: string, command: VoidFunction, description?: string, hidden?:boolean): BasicCommand{
    return {
        label, command, description, hidden
    }
}