import React from 'react';
import { flexRow } from '../../CSSStyles';
import { Datamap } from '../../engine/Datamap';
import Engine from '../../engine/Engine';
import { AUTOSAVE_INTERVAL, canCheat } from '../../engine/externalfns/util';
import ConfirmCommandButton from '../comps/ConfirmCommandButton';
import TipFC from '../comps/TipFC';


interface CompProps {engine:Engine, data:Datamap};
class HeaderRow extends React.Component<CompProps, {}> {

  render() {

    
    let engine = this.props.engine;
    let data = this.props.data;
    //let bg = background: 
    //picture no-repeat left top,picture no-repeat right center,picture repeat-x center bottom
    return (
      <div className='HeaderRow'>
      <div style={flexRow}>
      <span className='Tuser' style={{marginRight:'10px'}}>
        SomethingToCareAbout
      </span>
        <button onClick={engine.save} onContextMenu={(event)=>{
          engine.autosaveToggle()
          event.preventDefault();
        }}>
          {getSaveText(data)}
          <TipFC tip={<span>
            {`Right click to toggle autosave ${data.autosave?'(ON)':'(OFF)'}`}<br/>
            The game will still save at certain times regardless.
          </span>
            } />
        </button>

      </div>
      <div style={flexRow} className='RightHeaderRow'>
        <button onClick={engine.load}>
          LOAD
        </button>
        <span className='badButton'>
        <ConfirmCommandButton
        warning='There is no benifit. Just resets your data.'
        command={{
          able: ()=>true,
          command: engine.reset,
          hidden: ()=>false,
          description: ()=>"",
          label: 'RESET YOUR SAVE FILE'
        }}
        />
        </span>
        {canCheat() &&
        <button onClick={engine.firstCommands.toggleCheating}>
          TURBO
        </button>}
      </div>
      </div>
        
    );
  }
}

export default HeaderRow;

export function getSaveText (data:Datamap) {
  if (data.autosave) {
    return (

      <span>
      <span className={data.autosaveCounter/AUTOSAVE_INTERVAL > .2?'Tuser':'Ttext'}>S</span>
      <span className={data.autosaveCounter/AUTOSAVE_INTERVAL > .4?'Tuser':'Ttext'}>A</span>
      <span className={data.autosaveCounter/AUTOSAVE_INTERVAL > .6?'Tuser':'Ttext'}>V</span>
      <span className={data.autosaveCounter/AUTOSAVE_INTERVAL > .8?'Tuser':'Ttext'}>E</span>
    </span>
      )
  } else return (<span>SAVE</span>)
}