import React from 'react';
import { Datamap } from '../../engine/Datamap';
import Engine from '../../engine/Engine';
import { makeBasicCommand } from '../../engine/types/BasicCommand';
import CommandButton from '../comps/CommandButton';
import CommandGDButton from '../comps/CommandGDButton';
import ExpoButton from '../comps/GoodExpoButton';
import SCIENNOT from '../comps/Notation';
import * as Spells from '../../engine/modules/Spells';
import SpellDisplay from '../comps/SpellDisplay';
import EventRow from './EventRow';
import PopUpDiv from '../comps/PopUpDiv';
import PrestigeDiv from './PrestigeDiv';

interface CompProps {engine:Engine, data:Datamap};
class NavColumn extends React.Component<CompProps, {}> {

  render() {
    let engine = this.props.engine;
    let data = this.props.data;

    let discharge = makeBasicCommand('Discharge Y',engine.energyModule.discharge,`+${SCIENNOT.formatDecimal(engine.energyModule.dischargeValue())} X`,data.decimals.chargers.eq(0))

    let seeEventBox = data.events.eventID != 0 || data.events.lastEventResult != 0;

    return (
      <div className='NavColumn'>
        <div className='NavRow'>
        <ExpoButton DEC={engine.firstCommands.create_energy_DEC} engine={engine} max/>
        <ExpoButton DEC={engine.firstCommands.create_dx_fromY} engine={engine} />
        <ExpoButton DEC={engine.firstCommands.create_energizer_DEC} engine={engine} />
        <ExpoButton DEC={engine.firstCommands.create_charger_DEC} engine={engine}/>
        <ExpoButton DEC={engine.firstCommands.create_dx_fromZ} engine={engine} />
        <ExpoButton DEC={engine.firstCommands.create_momentum_DEC} engine={engine}/>
        </div>
        <div className='NavRow'>
          <CommandButton command={discharge}/>
          <SpellDisplay spell={engine.spells.increaseEffort} engine={engine}/>
          <SpellDisplay spell={engine.spells.spell_stca} engine={engine}/>
          <CommandGDButton command={engine.spells.unlock_mana} />
          <CommandGDButton command={engine.spells.unlock_spell1} />
          <CommandGDButton command={engine.spells.upgrade_discharge_1} />
          <ExpoButton DEC={engine.spells.upgradeDischarge} engine={engine}/>
        </div>
        {(data.events.examProgress > 0) && <div className='NavRow'>
          <ExpoButton DEC={engine.knowledgeUpgrades.upgradeDeltaX} engine={engine} />
          <ExpoButton DEC={engine.knowledgeUpgrades.resetLearning} engine={engine} />
          {engine.knowledgeUpgrades.basePrestigePoints > 0 && <PopUpDiv div={<PrestigeDiv engine={engine}/>} buttonText={`Spend ${engine.knowledgeUpgrades.prestigePoints} Prestige points`} />}
        </div>}
        {(seeEventBox)&&<EventRow data={data} engine={engine} />}

  
      </div>
        
    );
  }
}

export default NavColumn;